.cart-body {
  margin-bottom: 0.7em;
}

.cart-item {
  margin-bottom: 0.3em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cart-item-name {
  margin-left: 0.5em;
  vertical-align: middle;
}

.cart-item-price {
  font-weight: bold;
}

.cart-total {
  font-weight: bold;
  font-size: 1.5em;
  line-height: 1.1em;
  text-align: right;
}
